@import url(https://fonts.googleapis.com/css?family=B612+Mono:700i&display=swap);
@import url(https://fonts.googleapis.com/css?family=Alegreya&display=swap);
.form{
    margin: 30px 0px 20px 180px;
}

#search-field{
    min-width: 200px;
    min-height: 30px;
    border-radius: 4px;
    padding: 10px;
    box-sizing: border-box;
    border: none;
    border-bottom: 2px solid rgb(36, 154, 233);
    outline: none;
    font-weight: 300;
    font-size: 15px;
    box-shadow: 2px 8px 8px -5px rgb(36, 154, 233);
}

#submit-btn{
    font-size: 15px;
    padding: 10px;
    margin: 5px;
    min-width: 70px;
    min-height: 30px;
    border-radius: 4px;
    border: none;
    box-shadow: 2px 8px 8px -5px rgb(36, 154, 233);
    cursor: pointer;
}
#repo{
    margin: 100px 0px 50px 100px;
    display: grid;
    grid-gap: 50px;
    grid-template-columns: repeat(2, minmax(200px, 2fr));
    font-family: 'Alegreya', serif;
}
.white-repo-details{
    border-radius: 5px;
    width: 300px;
    min-height: 15vh;
    padding: 5px;
    box-shadow:
        0 2.8px 2.2px rgba(0, 0, 0, 0.034),
        0 6.7px 5.3px rgba(0, 0, 0, 0.048),
        0 12.5px 10px rgba(0, 0, 0, 0.06),
        0 22.3px 17.9px rgba(0, 0, 0, 0.072),
        0 41.8px 33.4px rgba(0, 0, 0, 0.086),
        0 100px 80px rgba(0, 0, 0, 0.12)
}
.dark-repo-details{
    border-radius: 5px;
    width: 300px;
    min-height: 15vh;
    padding: 5px;
    box-shadow:
        0 2.8px 2.2px rgba(255, 255, 255, 0.034),
        0 6.7px 5.3px rgba(255, 255, 255, 0.048),
        0 12.5px 10px rgba(255, 255, 255, 0.06),
        0 22.3px 17.9px rgba(255, 255, 255, 0.072),
        0 41.8px 40.4px rgba(255, 255, 255, 0.086),
        0 10px 8px rgba(255, 255, 255, 0.12)
}
.fork-star{
    display: flex;
    padding-right: 100px;
    padding-top: 5px;
}
#star{
    padding-left: 20px;
}
#description{
    padding-top: 5px;
}

.user-details{
    margin: 10px 0px 0px 80px;
    font-family: 'Alegreya', serif;
}


.user-photo > img{
    width:250px;
    border-bottom: 2px solid rgb(36, 154, 233);
    border-radius: 50%;
}
.user-photo > h3{
    margin: 5px 0px 10px 38px ;
    font-size: 25px;
}
.other-details{
    border: 2px solid black;
    border-style: none;
    width: 250px;
    font-size: 18px;
}
ul{
    list-style-type: none;
}
li{
    box-shadow: 0px 8px 8px -8px rgb(36, 154, 233);
}
li>span{
    float: right;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
h2{
    cursor: pointer;
}
.heading-main{
    display: flex;
}
#dark{
    margin-top: 20px;
    margin-left: 400px;
    width: 50px;
}
#heading{
    display: flex;
    margin: 20px 0px 0 180px;
    font-family: 'B612 Mono', monospace;
    font-size: 40px;
}
.user-repo{
    display: flex;
    justify-content: space-evenly;
    /* align-items: center; */
}

